import React,{useEffect,useState} from "react";
import { Link } from "react-router-dom";
import axios from 'axios'
import dateFormat from 'dateformat'
import CategoryNav from '../category_nav/category_nav'

import './videos.css'
import '../category_nav/category_nav.css'


function AllVideos(){
    const[videos,setVideos]=useState([])
    


    useEffect(()=>{
        axios({
            method:'GET',
            url:"http://127.0.0.1:8000/api/videos"
        }).then(response=>{
            setVideos(response.data)
            console.log(videos)
            
        })
    },[])
    const [value, setValue] = useState('')
    const filtersVideos = videos.filter(video =>{
        return video.name.toLowerCase().includes(value.toLowerCase())
    })

    return(
    <div className="content">
      <div className="conteiner__category">
      <CategoryNav/>
      <div className="form">
          <form className="serch__form">
              <input 
              
              type="text"
              placeholder="поиск"
              className="serch__input"
              onChange={(event)=>setValue(event.target.value)}
              />
            
          </form>
      </div>
          <div className="videos">
              {filtersVideos.map(video=>(
                  <div className="video__col">
                  
                    <div className="video">
                    <Link to={{pathname:`/videos/${video.id}`,fromDashboard:false}}>
                      <img className="video__img" src={video.preview} alt=""/>
                      </Link>
                      <div className="video__content">
                          <div className="video__user">
                              <a href="#">
                                  <img src="data:image/jpeg;base64," alt=""/>
                                </a>
                          </div>
                          <div className="video__tittle">
                              {video.name}
                              
                              <div className="user__name">{video.author}</div>
                              <div className="video__date">{dateFormat(video.published,"mmmm dS,yyyy")}</div>
                          </div>
                      </div>
          
                    </div>
                  
                  </div>
              ))}
          </div>
      

      </div>
      
    </div>
    )
}

export default AllVideos